*{
  font-family: Helvetica, Arial, sans-serif;
}
body{
  padding: 25px;
}

input{
  width: 100%;
  font-size: 1rem;
  padding: 7px;
}

.cards{
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
}

.card{
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  flex: 1 0px;
}

.card img{
  height: 250px;
  width: auto;
}